import { Value, Block } from '@ali/4ever-cangjie';

const getClosestParagraph = (
  value: Value,
  alignBlock?: Block,
): Block | null => {
  if (!value) return null;

  const { startBlock, document } = value;
  const block = alignBlock || startBlock;
  if (!startBlock || !block) return null;
  if (
    block.type === 'paragraph' ||
    block.type === 'heading-1' ||
    block.type === 'heading-2' ||
    block.type === 'heading-3' ||
    block.type === 'heading-4' ||
    block.type === 'heading-5' ||
    block.type === 'heading-6'
  ) {
    return block;
  }
  // @ts-ignore
  return document.getClosest(block.key, ({ type }) => type === 'paragraph') as Block;
};

export default getClosestParagraph;
